<template>
  <div class="view-box">
    <div id="toolbar">rotatingrotatestop</div>
    <div class="container">
      <vdrr :w="200" :h="200" :x="0" :y="0" :rotatable="true" @rotating="onRotating" @rotatestop="onRotateStop">
        <p>
          {{ rotating ? '' : '' }}
          <br />
          Rotate: {{rotate}}
        </p>
      </vdrr>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rotating: false,
      x: 0,
      y: 0,
      width: 200,
      height: 200,
      rotate: 0
    }
  },
  methods: {
    onRotating: function (degree) {
      this.resizing = true
      this.rotate = degree
    },
    onRotateStop: function (degree) {
      this.resizing = false
      this.rotate = degree
    }
  }
}
</script>

