<template>
  <div class="view-box">
    <div id="toolbar">activateddeactivated</div>
    <div class="container">
      <vdrr :w="200" :h="200" @activated="onActivated" @deactivated="onDeactivated">
        <p v-if="active"></p>
      </vdrr>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: false
    }
  },
  methods: {
    onActivated() {
      this.active = true
    },
    onDeactivated() {
      this.active = false
    }
  }
}
</script>

