// request/axios.js
import axios from "axios";
// 
axios.defaults.setTimeout == 5000;
// axios.defaults.baseURL = 'https://localhost';
// 
// 
axios.interceptors.request.use(
  function (config) {
    // 
    return config;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);

// 
axios.interceptors.response.use(
  function (response) {
    // 
    return response;
  },
  function (error) {
    // 
    return Promise.reject(error);
  }
);
export default axios;
